import postRobot from '../../../post-robot-wrapper';
import './dynamic-field.html';
import '../styles/defaults.css';
import {handleEvent, handleReturnKeyEvent, runEventOnElement} from '../input-events';
import { applyOptions } from '../options';
import { getComponentIdFromQueryString, getControllerIdFromQueryString } from '../querystring';
import {config} from '../config';
import {
  mountComponent,
  addTriggerEvent,
  addHandleOptions, addGetComponentData,
} from '../payment-events';
import {generateComponentData} from '../payment-component-data';
import {stripLetters} from '../utils';

const COMPONENT_TYPE = 'dynamicfield';

const componentData = generateComponentData(COMPONENT_TYPE,getComponentIdFromQueryString(COMPONENT_TYPE), getControllerIdFromQueryString());

const defaultOptions = {
  placeholderText: ''
};

const controllerListener = postRobot.listener({ window: componentData.controller.window, domain: config.domain });
const controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });

const triggerData = {
  componentData: componentData,
  getElement: getElement
};

mountComponent(controllerEmitter, componentData, handleOptions, emitComponentReady);
addHandleOptions(controllerListener, handleOptions);
addTriggerEvent(controllerListener, runEventOnElement, triggerData);
addGetComponentData(controllerListener, getElement);

/**
 * getElement returns the ccNumber dom element
 * @returns {HTMLElement}
 */
export function getElement() {
  return document.getElementById('field');
}

export function handleNumberChange(event) {
  const initialEventValue = event.target.value;
  event.target.value = stripLetters(initialEventValue);
  handleEvent(componentData, 'change', event);
}

function renderField(type, maxLength) {
  const inputField = document.createElement('input');
  inputField.className = 'base';
  inputField.id = 'field';
  inputField.oninput = type === 'number' ? handleNumberChange  : handleChange;
  inputField.onblur = handleBlur;
  inputField.onfocus = handleFocus;
  inputField.maxLength = maxLength;
  document.body.appendChild(inputField);
}

function renderTextArea() {
  const inputField = document.createElement('textarea');
  inputField.className = 'base';
  inputField.id = 'field';
  inputField.style.resize = 'none';
  inputField.style.height = '100%';
  inputField.oninput = handleChange;
  inputField.onblur = handleBlur;
  inputField.onfocus = handleFocus;
  document.body.appendChild(inputField);
}

export function resizeIframe(newSize) {
  const eventData = {
    frame: {
      height: newSize,
      id: componentData.componentId
    }
  };
  const event = new CustomEvent('resize', { detail: eventData });
  handleEvent(componentData, 'resize', event);
}

/**
 * handleOptions applies options to the dynamic field dom element
 * @param {object} data
 */
export function handleOptions(data) {
  addInstanceOptions(data.instanceOptions);

  componentData.componentType = data.options.componentType;
  componentData.required = data.options.isRequired;
  componentData.dynamic = true;
  componentData.sendValue = typeof data.options.sendValue !== 'undefined' ? data.options.sendValue : true;
  componentData.options = data.options;
  componentData.validationPattern = data.options.validationPattern;
  componentData.minLength = data.options.minLength;
  componentData.maxLength = data.options.maxLength;
  componentData.min = data.options.min;
  componentData.max = data.options.max;
  componentData.type = data.options.type;

  if (data.options.textArea === true) {
    renderTextArea();
    resizeIframe('64px');
  } else {
    const maxLength = typeof componentData.maxLength !== 'undefined' ? componentData.maxLength : 250;
    const type = componentData.type === 'number' || componentData.type === 'integer' ? 'number' : 'text';
    renderField(type, maxLength);
  }

  applyOptions(getElement(), data.options, defaultOptions);
}

/**
 * Stores the instance options in the component state
 * @param instanceOptions
 */
export function addInstanceOptions(instanceOptions) {
  componentData.instanceOptions = instanceOptions;
}


/**
 * handleBlur accepts a blur event and sends it on to handleEvent
 * @param {Event} event
 */
export function handleBlur(event) {
  handleEvent(componentData, 'blur', event);
}

/**
 * handleFocus accepts a focus event and sends it on to handleEvent
 * @param {Event} event
 */
export function handleFocus(event) {
  handleEvent(componentData, 'focus', event);
}

/**
 * handleChange accepts a change event, strips letters from the field value,
 * and if appropriate handles event, sets max length and applies space formatting
 * @param {Event} event
 */
export function handleChange(event) {
  handleEvent(componentData, 'change', event);
}

/**
 * handleAutofill accepts an autofill event and sends it on to handleEvent
 * @param {Event} event
 */
export function handleAutofill(event) {
  handleEvent(componentData, 'autofill', event);
}

/**
 * emitComponentReady calls handleEvent with 'ready'
 */
export function emitComponentReady() {
  handleEvent(componentData, 'ready');
}

export function keyUpEvent(event) {
  handleReturnKeyEvent(event, componentData);
}
